//.jcrop-vline, .jcrop-hline
.jcrop-line {
	background: #ffffff url('../images/Jcrop.gif');
	font-size: 0;
	position: absolute;
}

.jcrop-holder {
	direction: ltr;
	text-align: left;
	img {
		max-width: none;
	}
}

.jcrop-vline {
	@extend .jcrop-line;
	height: 100%;
	width: 1px !important;
	&.right {
		right: 0;
	}
}

.jcrop-hline {
	@extend .jcrop-line;
	height: 1px !important;
	width: 100%;
	&.bottom {
		bottom: 0;
	}
}

.jcrop-tracker {
	height: 100%;
	width: 100%;
}

.jcrop-handle {
	background-color: #333333;
	border: 1px #eeeeee solid;
	width: 7px;
	height: 7px;
	font-size: 1px;
	&.ord-n {
		left: 50%;
		margin-left: -4px;
		margin-top: -4px;
		top: 0;
	}
	&.ord-s {
		bottom: 0;
		left: 50%;
		margin-bottom: -4px;
		margin-left: -4px;
	}
	&.ord-e {
		margin-right: -4px;
		margin-top: -4px;
		right: 0;
		top: 50%;
	}
	&.ord-w {
		left: 0;
		margin-left: -4px;
		margin-top: -4px;
		top: 50%;
	}
	&.ord-nw {
		left: 0;
		margin-left: -4px;
		margin-top: -4px;
		top: 0;
	}
	&.ord-ne {
		margin-right: -4px;
		margin-top: -4px;
		right: 0;
		top: 0;
	}
	&.ord-se {
		bottom: 0;
		margin-bottom: -4px;
		margin-right: -4px;
		right: 0;
	}
	&.ord-sw {
		bottom: 0;
		left: 0;
		margin-bottom: -4px;
		margin-left: -4px;
	}
}

.jcrop-dragbar {
	&.ord-n {
		height: 7px;
		width: 100%;
		margin-top: -4px;
	}
	&.ord-s {
		height: 7px;
		width: 100%;
		bottom: 0;
		margin-bottom: -4px;
	}
	&.ord-e {
		height: 100%;
		width: 7px;
		margin-right: -4px;
		right: 0;
	}
	&.ord-w {
		height: 100%;
		width: 7px;
		margin-left: -4px;
	}
}

.jcrop-light {
	.jcrop-vline, .jcrop-hline {
		background: #ffffff;
		filter: alpha(opacity=70) !important;
		opacity: .70 !important;
	}

	.jcrop-handle {
		background-color: #000000;
		border-color: #ffffff;
		//Instead of the line below you could use @includeborder-radius($radius, $vertical-radius)
		border-radius: 3px;
	}
}

.jcrop-dark {
	.jcrop-vline, .jcrop-hline {
		background: #000000;
		filter: alpha(opacity=70) !important;
		opacity: 0.7 !important;
	}

	.jcrop-handle {
		background-color: #ffffff;
		border-color: #000000;
		//Instead of the line below you could use @includeborder-radius($radius, $vertical-radius)
		border-radius: 3px;
	}
}

.solid-line {
	.jcrop-vline, .jcrop-hline {
		background: #ffffff;
	}
}

img.jcrop-preview {
	max-width: none;
}

.jcrop {
	max-width: 100%;
	height: 300px;
	max-height: 300px;
	overflow: auto;
	text-align: center;
	vertical-align: middle
}